.card__collection {
  position: relative;
  display: inline-flex;
  /* background: rgba(10, 10, 10, 0.5); */
  /* padding: 1rem; */
  font-family: 'Do Hyeon', sans-serif;
  overflow: hidden;
  white-space: wrap;
}
.card__collection .cards {
  width: 13rem;
  height: 16rem;
  display: block;
  background-size: cover;
  float: left;
  border-radius: 15px;
  position: relative;
  overflow: hidden;
  background-position: center;
  margin: 0.625rem;
}
.card__collection .cards--three {
  position: relative;
  z-index: 10;
}
.card__collection .cards--three:hover .cards--three__rect-1 {
  left: 10%;
}
.card__collection .cards--three:hover .cards--three__rect-1 .shadow-1 {
  left: -20%;
}
.card__collection .cards--three:hover .cards--three__circle {
  transform: scale(1);
}
.card__collection .cards--three:hover .cards--three__circle:before {
  transform: scale(0.9);
}
.card__collection .cards--three:hover .cards--three__list li {
  transform: translateX(0);
}
.card__collection .cards--three__rect-1 {
  background: #fff;
  width: 11rem;
  height: 6rem;
  transform: skewX(-20deg);
  display: block;
  position: absolute;
  top: 73%;
  opacity: .9;
  left: -100%;
  z-index: 8;
  transition: all .5s ease-in;
}
.card__collection .cards--three__rect-1 .title-p {
  font-size: 1.2rem;
  color: #fff;
  transform: skewX(20deg);
  /* line-height: 0.8rem; */
}
.card__collection .cards--three__rect-1 .text-p {
  font-size: 0.56rem;
  color: #fff;
  text-align: left;
  transform: skewX(20deg);
  line-height: 0.8rem;
}
.card__collection .cards--three__rect-1 .shadow-1 {
  background:rgb(191, 66, 59);
  width: 12rem;
  height: 6rem;
  display: block;
  position: absolute;
  left: -200%;
  z-index: -1;
  transition: all .3s ease-in;
}
.card__collection .cards--three__circle {
  width: 13.5rem;
  height: 12rem;
  border-radius: 50%;
  background: #fff;
  position: absolute;
  top: -15%;
  left: 50%;
  opacity: .9;
  transform: scale(0);
  transition: all .3s ease;
}
.card__collection .cards--three__circle:before {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  background: rgb(191, 66, 59);
  border-radius: 50%;
  transform: scale(0);
  transition: all .3s ease;
  transition-delay: .2s;
}
.card__collection .cards--three__list {
  list-style: none;
  position: absolute;
  top: 0.9rem;
  text-align: center;
  right: 0.1rem;
  padding-top: 2rem;
  transition: all .4s ease;
}
.card__collection .cards--three__list li {
  display: inline-block;
  margin: 0.1rem;
  color: #fff;
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  /* background: rgba(87, 204, 253, 0.9); */
  text-align: center;
  line-height: 1.7rem;
  font-size: 0.75rem;
  transition: all .5s ease;
  transform: translateX(500%);
}
.card__collection .cards--three__list li:nth-child(1) {
  transition-delay: .2s;
}
.card__collection .cards--three__list li:nth-child(2) {
  transition-delay: .3s;
}
.card__collection .cards--three__list li:nth-child(3) {
  transition-delay: .4s;
}
.card__collection .cards--three__list li:nth-child(4) {
    transition-delay: .5s;
}
.card__collection .cards--three__list li:nth-child(5) {
    transition-delay: .6s;
}
